.contact{
    min-height: 100vh;
   
    background-color: #232123;
    position: relative;
    color: white;
    display: flex;
    justify-content: space-between;
    gap:50px;
    flex-direction: row;
    padding: 50px;
}
.cbackground{
    position: absolute;
    width: 25px;
    left: 0;
    height: 100%;
    background-color: green;
}
.contactcontent
{
    color: black;
}
.left{
    flex: 1;
}
.right{
    flex: 1;
}
.iconbar{
    display: flex;
    flex-direction: column;
    gap:30px;
    padding-top: 50px

}
.iconwrapper{
    font-size: 30;
    display: flex;
    flex-direction: row;
    gap:20px
}
.ctitle{
    font-size: 30;
    font-weight: 400;
}
.cdiscription{
    font-size: 15;
    font-weight: 200;

}

form {
    margin-top: 20px;
  }
  
  input {
    width: 100%;
    height: 50px;
    border: none;
    border-bottom: 1px solid black;
    margin: 10px 0px;
    font-size: 14px;
    padding-left: 10px;
    border-radius: 4px;
  }
  
  textarea {
    width: 100%;
    margin: 10px 0px;
    font-size: 14px;
    padding-left: 10px;
    border-radius: 4px;
  }

@media screen and (max-width: 480px) {
    .projectsbar{
        flex-direction: column;
    }
  
    .i-left-wrapper {
    
      padding: 10px;
      align-items: center;
      justify-content: center;
      text-align: center;
      height: 100%;
    }
  
    .background{
      display: none;
    }
    
}