.homepage{
    background-color: #232123;
    width: 100%;  
    overflow: hidden;

}
.introcomponent{

    height: 100vh;
    display: flex;
    align-items: center;
    justify-content: center;
    z-index: 1;
    padding: none;
    
}
.navbar{
    display: flex;
    justify-content: center;
    gap:40px;
    position: absolute;
    width: 100%;
    padding: 10px 0px;
}
.item{
    color: white;
    font-weight: bold;
    cursor: pointer;
}
.intro{
    color:white;
    display: flex;
    flex-direction: column;
    gap:20px;
    align-items: center;
    height: 30vh;
}
.background{
    position: absolute;
    width: 100%;
    height: 100vh;
    z-index: -1;
    object-fit: cover;
    overflow: hidden;
}
.buttonbar{
    display: flex;
    gap:20px;
    z-index: 1;
}
.projectsbutton{
    background-color: #f8d712;
    padding: 10px 20px;
    color:black;
    font-weight: bold;
    cursor: pointer;
}

.contactbutton{
    padding: 10px 20px;
    color:white;
    border: 1px solid white;
    background: rgba(51, 170, 51,  0) ;
    font-weight: bold;
    cursor: pointer;
}
.projects{
    gap:40px;
    color:white;
    z-index: -2;
    background-color:#232123;
    display: flex;
    flex-direction: column;
    align-items: center;
    padding-top: 50px;
   
}
.ptext{
    width: 40vw;
    text-align: center;
}
.projectsbar{
    display: flex;
    gap:40px;
    justify-content: center;

}
.projectitemwrapper{
    padding: 10px;
    border-radius: 3px;
    background-color: #343334;
    display: flex;
    flex-direction: column;
    gap:20px
}
.buttonbar{
    width: 100%;
    display: flex;
    justify-content: space-between;
}

.projectsitem{
    display: flex;
    gap:10px;
    flex-direction: column;

    position: relative;
}
.projectsimage{
    object-fit: cover;
    width:250px;
    height: 200px; 
}
.skillbar{

    display: flex;
    flex-direction: row;
    justify-content: space-between;
    margin-top: 80px;
    width: 80vw;
   
}
.sitem{
    display: flex;
    flex-direction: column;
    gap:30px;
    align-items: center;
}
.sitemimg{
    width: 150px;
    height: 150px;
    
}
.info{
    position: absolute;
    width: 100%;
    height: 100%;
    position: absolute;
    top: 0;
    left: 0;
    background-color: rgba(0, 0, 0, 0.5);
    z-index:5;
    opacity: 0;
    display: flex;
    justify-content: center;
    align-items: center;

}
.projectsitem:hover > .info{
    opacity: 1;
}
.contact{
    background-color: #101626;
    display: flex;
    justify-content: center;
    color:white;
    flex-direction: column;
    align-items: center;
    gap:10px
}
sub{
    color: #e3e3e3d6;
}
.inputwrapper{
    width: 40vw;
    margin-top: 40px;
    color: #e3e3e3d6;  
    border-radius: 4px;
    background-color: rgba(255, 255, 255, 0.5);
}
input{
    border: none;
    width: 100%;
    background-color: rgba(255, 255, 255, 0.5);
}
.aboutme{

    background-color: #232123;
    display: flex;
    justify-content: center;
    color:white;
    flex-direction: column;
    align-items: center;
    padding-bottom:200px;
    
}

.atitle {
    height: 40px;
    overflow: hidden;
    margin-bottom: 30px;
  }
  
  .atitlewrapper {
    height: 100%;
    animation: move 10s ease-in-out infinite alternate;
  }
  
  @keyframes move {
    25% {
      transform: translateY(-40px);
    }
    50% {
      transform: translateY(-80px);
    }
    75% {
      transform: translateY(-120px);
    }
    100% {
      transform: translateY(-160px);
    }
  }
  
  .atitleitem {
    height: 40px;
    font-size: 30px;
    font-weight: bold;
    color: #59b256;
    display: flex;
    align-items: center;
    justify-content: center;
}
.adiscription{
    width: 80vw;
}

@media screen and (max-width: 480px) {
    .projectsbar{
        flex-direction: column;
    }
  
    .i-left-wrapper {
    
      padding: 10px;
      align-items: center;
      justify-content: center;
      text-align: center;
      height: 100%;
    }
  
    .background{
      display: none;
    }
    .homepage{
        display: none;
    }
}
