.slider{
  height: 95vh;
  display: flex;
  gap:40px;
  width: 100%;
  padding: 0;
  overflow: hidden;
  background-color: #232123;
  position: relative;
}
.arrow-right{
 
  right: 100px;
  width: 50px;
  height: 50px;
  color: white;
  position: absolute;
  cursor: pointer;
  top:50%;
  z-index: 20;
}
.arrow-left{
  width: 50px;
  height: 50px;
  left: 100px;
  transform: rotateY(180deg);
  color: white;
  position: absolute;
  cursor: pointer;
  top:50%;
  z-index: 20;

}
.slideritem{
  width: 100vw;
  height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center;
 
  

}

.s-img{
  height:60vh;
  width: 50vw;
  object-fit: contain;
  border-radius: 40px;
  overflow: hidden;
  object-fit: fill;
  
}

.s-right{
  display: flex;
  flex-direction: column;

}
.s-title{
  font-size: 40px;
  margin-bottom: 40px;
  
}
.s-dis{
  font-size: 24px;
  margin-bottom: 60px;
}
.s-button{
  padding:25px 50px;
  font-size: large;
  border: 0.5px solid black;
  background-color: white;
  width: fit-content;
}